.promotion__list{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
    padding-right: 40px;
}
.promotion__item{
    width: 720px;
    margin-bottom: 100px;
    p{
        margin: 0;
    }
    h2{
        margin: 0;
    }
}
.promotion__date{
    color: #8e3a88;
    font-size: 16px;
    font-weight: 500;
    margin: 30px 0 15px 0;
    p{
        font-weight: 600;
    }
}
.promotion__title{
    color: #000000;
    font-size: 22px;
    margin-bottom: 14px;
    h2{
        font-weight: 700;
    }
}
.promotion__desc{
    color: #000000;
    font-size: 14px;
    line-height: 24px;
}
@media all and (max-width:1440px){
    .promotion__item{
        margin-bottom: 50px;
    }
}
@media all and (max-width:768px){
    .promotion__img{
        img{
            width: 500px;
        }
    }
    .promotion__item{
        width: 500px;
    }
    .promotion__title{
        font-size: 20px;
    }
    .promotion__date{
        margin: 15px 0 0 0;
    }
}
@media all and (max-width:452px){
    .promotion__item{
        width: 320px;
    }
    .promotion__img{
        img{
            width: 320px;
        }
    }
    .promotion__title{
        line-height: 32px;
    }
}
@media all and (max-width:320px){
    .promotion__item{
        width: 250px;
    }
    .promotion__img{
        img{
            width: 250px;
        }
    }
    .promotion__title{
        font-size:15px;
        margin-bottom: 5px;
    }
}