.BuiltIn {
    h1 {
      font-weight: bold;
    }
    .Filter_Icon {
      display: none;
    }
  
    .Filter-Container {
      display: flex;
      justify-content: flex-end;
    }
  
    margin-top: 10px;
    .BuiltIn_Header {
      display: flex;
      align-items: center;
      justify-content: space-between;
  
      .BuiltIn_Header__CustomSearchInput {
        position: relative;
        .searchButton {
          position: absolute;
          background-color: #797979;
          transform: translateX(-50%);
        }
        .ant-input {
          background-color: #f2f2f2;
        }
        .ant-input-group-addon {
          border-radius: 50%;
          .ant-btn-primary {
            background-color: #797979;
          }
        }
      }
    }
    .BuiltIn_LowerHeader {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 40px;
      margin-top: 20px;
      .LowerHeader_Sort {
        .Sort_Checkbox {
          margin-left: 20px;
        }
      }
      .LowerHeader_Icons {
        display: flex;
        .view_control{
          &:hover{
            cursor: pointer;
            svg{
              filter: invert(22%) sepia(50%) saturate(2807%) hue-rotate(281deg) brightness(89%) contrast(75%);
            }
          }
          &:first-child{
            margin-right: 20px;
          }
          &.active{
            svg{
              filter: invert(22%) sepia(50%) saturate(2807%) hue-rotate(281deg) brightness(89%) contrast(75%);
            }
          }
        }
      }
    }
  }
  
  @media all and (max-width: 992px) {
    .BuiltIn {
      .Filter_Icon {
        display: inline-block;
      }
      .LowerHeader_Sort {
        .Sort_Checkbox {
          margin-top: 10px;
          margin-left: 0 !important;
        }
      }
    }
  }
  
  @media all and (max-width: 633px) {
    .BuiltIn {
      .BuiltIn_LowerHeader{
        .LowerHeader_Icons{
          display: none;
        }
      }
      .BuiltIn_Header {
        .BuiltIn_Header-Container {
          display: flex;
          flex-direction: column-reverse;
          h1 {
            margin: 20px 0 0;
          }
        }
      }
    }
  }
  @media all and (max-width:550px){
    .BuiltIn{
      .BuiltIn_LowerHeader{
        .LowerHeader_Icons{
          display: none;
        }
      }
    }
  }
  @media all and(max-width:375px){
    .BuiltIn{
      .BuiltIn_LowerHeader{
        align-items: flex-end;
        .LowerHeader_Icons{
          display: none;
        }
      }
    }
    .list_view{
      .product_column{
        .Product{
          flex-direction: column;
        }
      }
    }
  }
  .list_view{
    .product_column{
      width: 100% !important;
      .Product{
        flex-direction: row;
        padding-right: 130px;
        &:hover{
            border-radius: 20px;
            box-shadow: 0px 0px 21px 0px rgba(0, 0, 0, 0.16);
            .imgContainer{
              border-top-left-radius: 20px;
              img{
                border-top-left-radius: 20px;
              }
            }
        }
        .product__block{
          display: flex;
          flex-direction: column;
          justify-content: flex-start;
          padding: 30px 0;
          &.product__info{
            .Product_Title{
              color: #000000;
              font-size: 24px;
            }
            .simpleText{
              font-size: 16px;
            }
            .price{
              font-size: 24px;
            }
          }
          .price{
            margin-top: auto;
          }
          &:last-child{
            margin-left: auto;
          }
        }
      }
    }
  }
  @media all and (max-width:1440px){
    .list_view{
      .product_column{
        .Product{
          padding-right: 50px;
          .product__block{
            &.product__info{
              padding-right: 15px;
            }
          }
        }
      }
    }
  }