.ProductFilter {
    .input_wrapper{
      position: relative;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      input{
        padding-left: 25px;
      }
      &.after{
        &::before{
          position: absolute;
          content: 'от';
          left: 5px;
          z-index: 10;
        }
      }
      &.before{
        &::before{
          position: absolute;
          content: 'до';
          left: 5px;
          z-index: 10;
        }
      }
    }
    .Filter_more{
      color: #187ac7;
    }
    .Filter_Reset{
      color: #126ada;
    }
    .Filter_Show{
      border: 1px solid #bcbcbc;
      border-radius: 5px;
      background-color: #f2f2f2;
      color: #000000;
      font-weight: 700;
    }
    .price_input{
      position: relative;
      &::before{
        position: absolute;
        content:'до';
      }
    }
    b {
      margin-bottom: 10px;
      color: #000000;
      font-size: 13px;
    }
    border: 1px solid #bcbcbc;
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    .ProductFilter_Title {
      padding: 10px 10px 0 10px;
    }
    .ant-checkbox-wrapper {
      margin: 0 0 8px 0;
      span{
        color: #000000;
      }
    }
    .Product_Attribute {
      padding: 10px;
      display: flex;
      flex-direction: column;
      border-bottom: 1px solid #bcbcbc;
      b {
        word-wrap: break-word;
      }
    }
    .Button-Container {
      display: flex;
      flex-direction: column;
      padding: 10px;
      text-align: center;
      a {
        margin-top: 15px;
      }
    }
  }
  
  .ProductFilter.__Drawer {
    border: none;
  }

  
  .CustomDropDown {
    .CustomDropDown-Container {
      margin-top: 10px;
  
      .CustomDropDown_Title {
        display: flex;
        justify-content: space-between;
        h3{
          font-weight: 700;
          font-size: 24px;
        }
      }
      .CustomDropDown_Description {
        padding: 10px 0;
        span{
          font-size: 16px;
          font-weight: 400;
          line-height: 26px;
          color:#000000;
        }
      }
    }
  }

  .CustomSearchInput {
    position: relative;
    width: 100%;
    padding-right: 16px;
  
    .searchButton {
      position: absolute;
      background-color: #797979;
      transform: translateX(-50%);
    }
    .ant-input {
      background-color: rgba(#000000,0.05);
      &::placeholder{
        color: rgba(#000000,0.5);
      }
    }
    .ant-input-group-addon {
      border-radius: 50%;
      .ant-btn-primary {
        background-color: #797979;
      }
    }
  }
  @media only screen and (max-width:320px){
    .CustomSearchInput{
      margin-bottom: 20px;
    }
  }
  

  .CustomCarousel {
    .CustomCarousel_ImgContainer {
      position: relative;
      width: 100%;
      height: 300px;
      display: flex;
      align-items: center;
      justify-content: center;
      img {
        display: block;
        position: absolute;
        width: 100%;
        height:100%;
        object-fit: cover;
        margin: 0 auto;
        left: 50%;
        transform: translateX(-50%);
      }
    }
  
    .ant-carousel .slick-slide {
      text-align: center;
      height: 100%;
      margin-bottom: 100px;
      & > div {
        height: 100%;
      }
    }
    .CustomCarousel_Container {
      position: relative;
  
      .Dots_Navigation {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        z-index: 2;
        &.__Left {
          left: 0;
        }
        &.__Right {
          right: 0;
        }
      }
      .CustomCarousel_Dots {
        width: 100%;
        display: flex;
        justify-content: space-evenly;
  
        position: relative;
        padding: 0 20px;
      }
  
      .CustomCarousel_Dots_Item {
        height: 70px;
        display: flex;
        align-items: center;
        overflow: hidden;
        padding: 10px;
        &.__Active {
          outline: 1px solid black;
        }
        width: 18%;
        img {
          width: 100%;
        }
      }
    }
  
    .ant-carousel .slick-slide {
      text-align: center;
      height: 300px;
      line-height: 160px;
      overflow: hidden;
    }
  
    .ant-carousel .slick-slide h3 {
      color: #fff;
    }
  }
  