.checkout__row{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 25px;
    flex-wrap: wrap;
    .btn-one{
        margin-left: auto;
    }
}
.page-title{
    h2{
        color: #000000;
        font-size: 36px;
        font-weight: 600;
        margin: 0;
       
    }
}
.btn{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 40px;
    width: 170px;
    border-radius: 20px;
    font-size: 14px;
    font-weight: 400;
    &-one{
        border: 1px solid #8e3a88;
        color: #000000;
    }
    &-two{
        background-color: #8e3a88;
        color: #ffffff;
    }
    &-cancel{
        border: 1px solid #e50000;
        color: #000000;
    }
}
.order__text{
    color: #000000;
    font-size: 20px;
    font-weight: bold;
    span{
        font-weight: 400;
    }
    &-middle{
        font-size: 18px;
        font-weight: 600;
    }
    &-small{
        font-size: 14px;
        font-weight: 600;
    }
    &-big{
        font-size: 24px;
        font-weight: bold;
    }
    .order__link{
        color: #523bc5;
        text-decoration: underline;
    }
    .accent-color{
        color: #8e3a88;
        font-weight: 600;
    }
}
.order__textarea{
    height: 320px;
    width: 769px;
    border-radius: 20px;
    padding: 30px;
    font-size: 18px;
    color: #000000;
    border:none;
    background-color: #f2f2f2;
    margin-bottom: 30px;
}
.btn{
    &-cancel::before{
        background: url(../img/close.png) no-repeat center;
        height: 12px;
        width: 12px;
    }
    &-cancel{
        position: relative;
        &:before{
            margin-right: 10px;
            height: 13px;
            width: 13px;
            content: '';
        }
    }
}
@media all and (max-width:768px){
    .order__row-title{
        flex-direction: column;
    }
}
@media all and (max-width:425px){
    .checkout__row{
        justify-content: center;
        flex-direction: column;
        .btn-one{
            margin: unset;
            margin-top: 15px;
        }
    }
    .order__container{
        padding: 0 15px;
    }
}
.order-table__row{
    display: flex;
    justify-content: space-between;
    align-items:flex-start;
    padding:0px 0 46px 0px;
    border-bottom: 1px solid rgba(#000000,0.1);
}
.order__row-title{
    flex-wrap: wrap;
}
.order-table__column{
    flex: 25%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.order__rowContent{
    .order__text{
        &-small{
            font-size: 16px;
            font-weight: 400;
        }
    }
}
.order__rowContent{
    width: 100%;
}
.order__table{
    .order__rowTitle{
        margin-top: 46px;
    }
    .mg-b-10{
        margin-top: 10px;
    }
    .btn-one{
        margin-top: 46px;
        border-color: black;
    }
    .btn-cancel{
        margin-top: 15px;
    }
}
@media all and (max-width:768px){
    .order-btn__column{
        margin-top: 15px;
    }
    .order-table__row{
        flex-direction: column;
    }
    .order-table{
        .btn-one{
            margin:10px 0;
        }
    }
    .order__row-title{
        .btn{
            margin: unset;
            margin-top: 20px;
        }
    }
    .order__textarea{
        width: 90%;
    }
}
@media all and (max-width:425px){
    .order__title,.order__text{
      font-size: 17px;
    }
    .order__text-middle{
      font-size: 15px;
    }
    .order__text-small{
        font-size: 15px;
    }
    .order__container{
        padding: 0 15px;
    }
    .page-title{
        h2{
            font-size: 22px;
        }
    }
  }
  @media all and(max-width:320px){
    .order__title{
      padding: 40px 15px;
    }
    .order__content{
      .order__row{
        padding: 0 10px;
      }
    }
  }
  .order__list{
    display: flex;
    .order__list__item{
        margin-left: 25px;
        &:first-child{
            margin-left: 0;
        }
}
}
.order__link{
    font-size: 24px;
    font-weight: bold;
    color: #523bc5;
}
@media all and (max-width:425px){
    .order__list{
        flex-direction: column;
        .order__list__item{
            margin: 0;
            margin-bottom: 10px;
            &:last-child{
                margin-bottom: 0;
            }
        }
    }
}
.order{
    box-shadow: 0 0 21px rgba(0, 0, 0, 0.16);
    border-radius: 20px;
    padding-bottom: 60px;
    margin-bottom: 40px;
    &:last-child{
        margin-bottom: 0;
    }
    p{
        margin-bottom: 0;
    }
}
.column__status{
    width: 30%;
}
.order-table{
    margin-top: 60px;
}
.order__title{
    border-radius: 20px 20px 0 0;
    background-color: #ebebeb;
    padding: 40px 55px;
    color: #000000;
    font-size: 20px;
    .order__number{
        font-weight: bold;
    }
    .order__info{
        font-weight: 600;
    }
}
.order__content{
    margin-top: 47px;
    .order__row{
        display: flex;
        justify-content: space-between;
        padding: 0 55px;
        &-title{
            margin-bottom: 60px;
        }
        &.content{
            margin-top: 15px;
            &:first-child{
                margin-top: 0;
            }
        }
    }
    .btn-column{
        display: flex;
        .btn{
            margin-left: 50px;
            width: 200px;
            &:first-child{
                margin-left: 0;
            }
            &-one{
                border: 1px solid #000000;
                color: #000000;
            }
            &-two:before{
                color: #ffffff;
                background: url(../img/refresh.png) no-repeat center;
            }
            &-cancel::before{
                background: url(../img/close.png) no-repeat center;
                height: 12px;
                width: 12px;
            }
            &-two,&-cancel{
                position: relative;
                &:before{
                    margin-right: 10px;
                    height: 13px;
                    width: 13px;
                    content: '';
                }
            }
        }
    }
}
.order__rowTitle{
    color: #523bc5;
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 0px;
}
@media all and (max-width:768px){
    .order__content{
        .order__row{
            flex-wrap: wrap;
            &.btn-row{
                justify-content: center;
                .order__column:first-child{
                    margin-bottom: 15px;
                }
            }
        }
    }
}
@media all and (max-width:425px){
    .column__status{
        width: unset;
    }
    .order__content .order__row.btn-row .order__column:first-child{
        margin-bottom: 0;
    }
    .order__content{
        .btn-column{
            display: flex;
            flex-direction: column;
            margin-bottom: 0;
            .btn{
                margin-left: 0px;
                &:first-child{
                    margin-bottom: 15px;
                }
            }
        }
    }
}
