.group{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    .checkout__row{
        width: 100%;
    }
.contacts__item{
    width: 46.5%;
    justify-content: flex-start;
    padding-right: 0;
    box-shadow:none;
    transition: .3s all;
    &:hover{
        box-shadow: 0 0 21px rgba(0, 0, 0, 0.16);
        cursor: pointer;
    }
    .contacts__wrapper{
        justify-content: flex-start;
    }
}
.contacts__block-first{
    margin-right:27px;
}
.group-margin{
    margin:5px 0 10px 0;
}
}
@media all and (max-width:768px){
.group{
    .CustomSearchInput{
        margin-bottom: 15px;
    }
    .checkout__row{
        width: 100%;
    }
}
}
@media all and (max-width:425px){
.group{
    .contacts__item{
        width: 100%;
    }
}
}