
.ProductPreview {
    @include shadowBlock;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 300px;
    &:hover{
      box-shadow: 0 0 21px rgba(142, 58, 136,0.16);
      .ProductPreview_Tiltle{
        color: #8e3a88;
      }
    }
    .imgContainer {
      margin-top: 10%;
      width: 90%;
      height: 80%;
      position: relative;
      overflow: hidden;
      img {
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }
    .ProductPreview_Tiltle {
      padding: 10px;
      text-align: center;
      font-size: 20px;
    font-weight: 400;
    color: #000000;
    }
  }
  @media only screen and (max-width:320px){
    .ProductPreview{
      margin-bottom: 20px;
    }
  }