.ant-table-tbody > tr > td{
  padding:40px 16px;
}
.Busket {
  margin-top: 10px;
  .Busket_Header {
    display: flex;
    align-items: center;
    justify-content: space-between;

    h1 {
      margin: 0;
      font-weight: bold;
    }
    margin-bottom: 20px;
  }

  .Busket_Table {
    border-radius: 20px;
    background-color: rgb(255, 255, 255);
    box-shadow: 0 0 21px rgba(0, 0, 0, 0.16);
    overflow: hidden;
    font-weight: 700;
    td{
      font-weight: 700;
      color: #000000;
    }
    .Product_Description{
      h3{
        font-weight: 700;
        color: #000000;
      }
      h3:first-child{
        margin-bottom:0;
      }
    }
    .Product_Quantity{
      .Quantity_Status{
        font-weight: 700;
        color:#000000;
      }
    }
    .Table_Footer {
      display: flex;
      justify-content: space-between;
      background-color: rgb(235, 235, 235);
      border-radius: 20px;
      padding: 45px;
      align-items: flex-start;
      .Input-Container {
        display: inline-flex;
        align-items: flex-end;

        .Input-Wrapper {
          width: 250px;
          flex-direction: column;
          h3 {
            font-weight: bold;
            margin-bottom: 20px;
          }
          .Input {
            border-radius: 20px;
          }
        }
        .Input-Button {
          background-color: black;
          color: white;
          border-radius: 20px;
          padding: 0 30px;
          margin-left: 7px;
        }
      }
      .Footer_Chekout {
        display: inline-flex;
        .Checkout_Amount {
         
          .Amount_Title {
            display: flex;
            justify-content: center;
            align-items: center;
            color: #000000;
            font-size: 14px;
            font-weight: bold;
            line-height: 18px;
            margin-bottom: 30px;
            b {
              margin-left: 8px;
              color: #000000;
              font-size: 24px;
              font-weight: bold;
              line-height: 48px;
            }
          }
          .Amount_Button {
            background-color: rgb(142, 58, 136);
            border-radius: 20px;
            height: 40px;
            padding:0 30px;
            color: white;
            span{
              font-size: 14px;
              font-weight: 400;
            }
          }
        }
      }
    }
  }
}

@media all and (max-width: 768px) {
  .Busket {
    .Busket_Header {
      .Busket_Header-Container {
        display: flex;
        flex-direction: column-reverse;
        h1 {
          margin: 20px 0 0;
        }
      }
    }
    .Busket_Table {
      .Table_Footer {
        flex-direction: column;
        align-items: center;
        .Input-Container {
          flex-direction: column;
          align-items: center;
          .Input-Wrapper{
            padding: 0 15px;
            h3{
              font-size: 14px;
              text-align: center;
            }
          }
          .Input-Button {
            margin:0;
            margin-top: 20px;
            margin-bottom: 10px;
          }
        }
        .Footer_Chekout {
          justify-content: center;
          .Amount_Title {
            display: flex;
            justify-content: center;
            margin-bottom: 10px !important;
          }
        }
      }
    }
  }
}
.table_title{
    font-size: 24px;
    font-weight: bold;
    line-height: 30px;
    color: #000000;
    margin-left: 60px;
    margin-top: 40px;
    margin-bottom: 45px;
  }
  .close_product{
    background: url(/img/close.svg) center center no-repeat;
    height: 15px;
    width: 15px;
    display: block;
    cursor: pointer;
  }
  .BusketTable {
    .ant-table-thead{
      tr:first-child{
        th:first-child{
          width: 500px;
        }
      }
      tr{
        th{
          background: none;
          border:none;
          font-size: 14px;
          font-weight: 400;
          color: #000000;
        }
      }
    }
    .ant-table-tbody{
      tr{
        position: relative;
        td{
          position: relative;
          border-bottom: 1px solid rgba(#000000,0.05);
          &:first-child{
            &::after{
              position: absolute;
              bottom: -1px;
              left: 0;
              background-color: white;
              width: 20px;
              height: 1px;
              content: '';
            }
          }
          &:last-child{
            &::before{
              position: absolute;
              bottom: -1px;
              right: 0;
              background-color: white;
              width: 20px;
              height: 1px;
              content: '';
            }
          }
        }
        &:last-child{
          td{
            border-bottom:none;
          }
        }
      }
    }
    .TableCell_Product {
      display: flex;
      .Product_ImageContainer {
        max-height: 150px;
        width: 150px;
        margin-right: 20px;
        position: relative;
        img {
          // height: 100%;
          width: 100%;
          position: absolute;
          transform: translateY(-50%);
          top: 50%;
        }
      }
      .Product_Quantity {
        display: flex;
        align-items: center;
        .Quantity_StatusIcon {
          width: 15px;
          height: 15px;
          background-color: #139918;
          margin-right: 5px;
          border-radius: 50%;
        }
      }
    }
    .TableCell_Count {
      display: flex;
      .TableCell_Count_Number {
        margin-right: 40px;
      }
      .Buttons-Container {
        display: flex;
        flex-direction: column;
      }
    }
  }
  @media screen and (min-width:1919px){
    .ant-table-body{
    overflow-x:auto!important;  
    overflow-y: hidden;
    }
  }