.Product {
    display: flex;
    flex-direction: column;
    font-family: 'Roboto';
    padding:10px;
    &:hover{
      border-radius: 20px;
      box-shadow: 0px 0px 21px 0px rgba(0, 0, 0, 0.16);
      .imgContainer{
        border-top-left-radius: 20px;
        border-top-right-radius: 20px;
        img{
          border-top-left-radius: 20px;
          border-top-right-radius: 20px;
        }
      }
  }
    .product__block{
      display: flex;
      flex-direction: column;
    }
    .imgContainer{
      margin-bottom: 15px;
    }
    .Product_Title {
      font-size: 18px;
      font-family: 'Roboto';
      font-weight: 400;
      margin-bottom: 0px;
    }
    .__margin {
      color: #8e3a88;
    }
    .price {
      font-size: 24px;
      margin-top: 20 px;
      font-weight: 400;
    }
    .quantity {
      font-size: 14px;
      margin-bottom: 20px;
    }
    .simpleText {
      font-size: 14px;
      margin-bottom: 4px;
      font-weight: 300;
      .changeMarkup {
        color: #0966d9;
        border-bottom: 1px dotted #0966d9;
      }
    }
  }
  