.profile-block{
    background-color: #ffffff;
    border-radius: 20px;
    box-shadow: 0 0 21px rgba(0, 0, 0, 0.16);
    margin-top: 40px;
    &:first-child{
        margin-bottom: 50px;
    }
}
.profile-block__title{
    background-color: #ffffff;
    border-radius: 20px;
    color: #000000;
    font-size: 24px;
    font-weight: bold;
    padding:0 55px;
    height: 100px;
    display: flex;
    position: relative;
    align-items:center;
    &:after{
        position: absolute;
        content:'';
        right: 50px;
        top: 40px;
        background:url(../img/arrow.png) center center no-repeat;
        width: 38px;
        height: 20px;
        transition: .3s all;
    }
    &.open{
        border-radius: 20px 20px 0 0;
        background-color: #ebebeb;
        &:after{
            transform: rotate(180deg);
        }
    }
}
.profile-block__content{
    padding: 27px 56px;
    display: none;
    &.open{
        display: block;
    }
}
.profile-block__row{
    display: flex;
    margin-bottom: 40px;
    align-items: center;
    &-big{
        margin: 80px 0;
    }
    &.profile-block__status{
        margin-bottom: 25px;
    }
    p{
        margin: 0;
    }
}
.profile-block__column{
    width: 575px;
}
.profile__text{
    color: #000000;
    font-size: 16px;
    font-weight: 500;
    &.profile__date{
        font-weight: bold;
    }
}
.profile__input{
    width: 450px;
    border-radius: 20px;
    background-color: #f2f2f2;
    font-size: 16px;
    color: #000000;
    font-weight: 600;
    border:none;
    outline: none;
    padding: 0 21px;
    height: 40px;
    &:focus{
        background-color: #ffffff;
        box-shadow: 0 0 21px rgba(142, 58, 136, 0.16);
    }
}
.profile-footer__title{
    color: #000000;
    font-size: 24px;
    font-weight: bold;
    span{
        color: #523bc5;
    }
}
.profile__select{
    font-size: 16px;
    color: #000000;
    font-weight: 600;
    .ant-select-selection--single{
        height: 40px;
        width: 450px;
        border-radius: 20px;
        background-color: #f2f2f2;
        display: flex;
        align-items:center;
        border:none;
        padding-left: 5px;
    }
}
.social-list{
    display: flex;
}
.social-list__item{
    margin-right: 20px;
}
.profile-block__row-button{
    .btn{
        margin-left: 50px;
        &:first-child{
            margin-left: 0;
        }
    }
}
.profile-footer__block{
    margin-top: 80px;
}
@media screen and (max-width:768px){
    .profile__input{
        margin-top: 10px;
    }
    .profile__select{
        margin-top: 10px;
    }
    .profile-block__row{
        flex-wrap: wrap;
    }
}
@media screen and (max-width:425px){
    .profile-block__row-button{
        flex-direction: column;
        .btn{
            margin: 0;
            &:first-child{
                margin-bottom: 20px;
            }
        }
    }
    .profile-block__row{
        &-big{
            margin: 40px 0;
        }
    }
    .profile__input{
        width: 250px;
    }
    .profile__select{
        .ant-select-selection--single{
            width: 250px;
        }
    }
    .profile-block__title{
        font-size: 20px;
    }
}
@media screen and (max-width:375px){
    .profile-block__content{
        padding:27px;
    }
    .profile-block__title{
        &:after{
            display: none;
        }
    }
}
@media screen and (max-width:320px){
    .profile-block__content{
        padding:27px 20px;
    }
    .profile-block__title{
        font-size: 18px;
        &:after{
            display: none;
        }
    }
    .profile__input{
        width: 200px;
    }
    .profile__select{
        .ant-select-selection--single{
            width: 200px;
            overflow: hidden;
        }
    }
}
.profile{
    .contacts__list{
        display: flex;
    }
    .contacts__item{
        padding: 40px 0 40px 0;
        flex: 1;
        margin-left:35px;
        box-shadow: none;
        transition: .3s all;
        &:hover{
            box-shadow: 0 0 21px rgba(0, 0, 0, 0.16);
            cursor: pointer;
        }
        &:first-child{
            margin-left:0;
        }
    }
    .contacts__wrapper{
        display: flex;
        flex-direction:column;
        align-items:center;
        justify-content: space-around;
    }
    .contacts_img{
        height: 110px;
    }
    .contacts__block{
        margin: 0;
    }
    
}
@media all and (max-width:768px){
    .profile{
        .contacts__list{
            flex-direction:column;
        }
        .contacts__item{
            margin: 0;
            width: 100%;
            margin-bottom: 25px;
        }
    }
}