@import url('https://fonts.googleapis.com/css?family=Roboto&display=swap');

body {
  font-family: 'Roboto', sans-serif !important;
}
.App {
  font-family: 'Roboto', sans-serif;
  a {
    //color: black;
    &:hover {
      color: black;
    }
  }
  ul {
    padding: 0;
    margin: 0;
  }
  li {
    list-style-type: none;
  }
}

.CatalogDisplay {
  width: 100%;
  padding: 0 40px;
  margin-top: 15px;
  .toggleSidebar {
    display: none;
  }
}

@media all and (max-width: 768px) {
  .CatalogDisplay {
    .toggleSidebar {
      display: inline-block;
    }
  }
}

.BuiltInAppliances {
  h1 {
    font-weight: bold;
  }
}
.MainHeader_Lower{
  
}
#main_nav{
  display: flex;
  justify-content: space-between;
  li{
    white-space: nowrap; /* Запрещаем перенос строк */
    overflow: hidden; /* Обрезаем все, что не помещается в область */
    padding: 5px; /* Поля вокруг текста */
    text-overflow: ellipsis; /* Добавляем многоточие */
  }
}

.toggleSidebar{
  position: absolute; 
  top: 125px;
  left: 40px;
  z-index: 100;
  display: none;
}

.menu__triger{
  display: none;
}
@media all and (max-width:768px){
#main_nav{
  display: none;
  &.open{
    display: block;
  }
}
.menu__triger{
  display: block;
  a{
    &:hover{
      color:white; 
    }
  }
}
.toggleSidebar{
  display: block;
}
}
.ant-drawer{
  display: none;
  left:0;
}
