.contacts{
    .checkout__row{
        justify-content: space-between;
        margin-bottom:0;
        &.flex-start{
            width: 60%;
        }
    }
    .CustomSearchInput{
        width: 500px;
    }
}
.settings{
    margin-right: 70px;
}
.list-actions{
    color: #040404;
    font-size: 16px;
    font-weight: 600;
    margin: 0;
}
.ellipsis-block{
    display: flex;
    align-items:center;
}
.ellipsis{
    display: flex;
    justify-content: flex-start;
    margin-right: 10px;
    >span{
        height: 4px;
        width: 4px;
        border-radius:50%;
        background-color: #000000;
        margin-left: 4px;
        &:first-child{
            margin-left: 0;
        }
    }
}
.contacts__list{
    margin-top:35px;
}
.contact__row{
    display: flex;
    .contacts__column:first-child{
        width: 100px;
    }
}
.contact-tab{
    display: flex;
    justify-content: flex-start;
    margin: 15px 0 44px 0;
}
.contact-tab__item{
    margin-left: 30px;
    font-size: 18px;
    font-weight: bold;
    a{
        color: #000000;
    }
    &.active{
        a{
            color: #523bc5;
        }
    }
    &:first-child{
        margin:0;
    }
}
.contacts__item{
    box-shadow: 0 0 21px rgba(142, 58, 136, 0.16);
    border-radius: 20px;
    background-color: #ffffff;
    margin-bottom: 40px;
    padding-right: 215px;
    padding-left: 40px;
    padding-bottom: 40px;
}
.contacts__wrapper{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.contacts__block{
    margin-top: 63px;
    .contact__block{
        margin-top: 23px;
        margin-left: 23px;
    }
    &-first{
        margin-top: 40px;
        display: flex;
    }
}
.contacts__text{
    margin: 0;
    font-size: 14px;
    color: #040404;
    &-title{
        color: #000000;
        font-size: 22px;
        font-weight: 700;
    }
    &-role{
        color: #8e3a88;
        font-size: 12px;
        font-weight: 600;
    }
    &-status{
        color: #000000;
        margin: 0px 0 5px 0;
        &:before{
            content:'';
            display: inline-flex;
            width: 9px;
            height: 9px;
            background-color: #66aa05;
            border-radius: 50%;
            margin-right: 9px;
        }
    }
    &-bold{
        font-weight: 600;
    }
}
@media screen and (max-width:1024px){
    .contacts__list{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    .contacts__item{
        width: 49%;
    }
}
@media screen and (max-width:768px){
    .ellipsis-block{
        margin-top: 15px;
    }
    .contacts{
        .CustomSearchInput{
            margin-top: 15px;
        }
    }
    .contacts .checkout__row.flex-start{
        width: 100%;
    }
    .settings{
        margin: 0;
    }
    .settings__row{
        flex-direction: column;
    }
}
@media screen and (max-width:425px){
    .contacts__item{
        width: 100%;
    }
    .contacts{
        .CustomSearchInput{
            margin-top: 15px;
            width: 100%;
        }
    }
    .contact-tab{
        margin:0px 0 15px 0;
        flex-direction: column;
    }
    .contacts .checkout__row.flex-start{
        width: 100%;
    }
    .contact-tab__item{
        margin: 0;
    }
}
@media screen and (max-width:375px){
    .contact-tab{
        margin:15px 0 15px 0;
        flex-direction: column;
    }
}
@media screen and (max-width:320px){
    .contacts__block{
        &-first{
            flex-direction: column;
            align-items:center
        }
    }
    .contact__row{
        flex-direction: column;
    }
    .contacts__column{
        margin-bottom:5px;
    }
}