.return_link{
    margin-top: 5px;
    font-size: 18px;
    font-weight: 500;
    margin-left: 25px;
    a{
      margin-left: 10px;
    }
  }
  .ant-carousel{
    margin-top: 100px;
  }
  .ProductCard {
    .ProductCard_Bonuses {
      display: flex;
      align-items: center;
      flex-direction: column;
      margin-top: 43px;
      .Bonuses_Item {
        border-radius: 50%;
        height: 70px;
        width: 70px;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        font-size: 14px;
        margin-top: 30px;
      }
    }
    .Basket_Container{
      display: flex;
      flex-wrap: wrap;
      margin-top: 30px;
      margin-bottom: 80px;
      .Basket_Input{
        margin-right: 20px;
        border-radius: 20px;
        border: 1px solid #f2f2f2;
        font-size: 18px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100px;
        height: 40px;
        color:#000000;
        .Increase_Basket{
          color:rgba(#000000,0.5);
          width: 40px;
          text-align: center;
          &:hover{
            cursor: pointer;
          }
        }
      }
      .Basket_Submit{
        width: 170px;
        height: 40px;
        border-radius: 20px;
        background-color: #8e3a88;
        font-size: 14px;
        color:#Ffff;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        padding-left: 20px;
        a{
          color:#fff;
        }
      }
    }
    .ProductCard-Container {
      margin-top: 72px;
      display: flex;
      flex-direction: column;
      font-family: 'Roboto';
      h1{
        font-size: 36px;
        font-weight: 500;
        margin-bottom:0;
        color:#000000;
      }
      .simpleText.__margin {
        color: #8e3a88;
      }
      .price {
        margin-top: 25px;
        font-weight: 500;
        display:flex;
        align-items: center;
        justify-content: flex-start;
        .new_price{
          font-size: 36px;
        }
        .old_price{
          margin-left: 15px;
          font-size: 18px;
          color: #000000;
          font-weight: 400;
          text-decoration: line-through;
        }
      }
      .quantity {
        font-size: 14px;
        margin-bottom: 11px;
        margin-top: 10px;
      }
      .simpleText.categoryProduct{
        font-size: 24px;
        font-weight: bold;
      }
      .simpleText {
        font-size: 16px;
        font-weight: 400;
        margin-top: 5px;
        color:#000000;
        .changeMarkup {
          color: #0966d9;
          margin-left: 10px;
        }
      }
  
    }
  
    .ProductCard__Download {
      margin: 30px 0;
      span {
        margin-left: 10px;
      }
    }
  
    @media all and (max-width: 768px) {
      .ProductCard_Bonuses {
        flex-direction: row;
        justify-content: space-evenly;
        margin-top: 0;
      }
    }
  }
  